import { BrowserRouter, Link, Route, Routes, NavLink } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Nav, NavDropdown } from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';
import { useContext, useEffect, useState } from 'react';
import { Store } from './Store.js';
import {
  HomeScreen,
  MapScreen,
  UserEditScreen,
  UserListScreen,
  OrderListScreen,
  BlogEditScreen,
  BlogListScreen,
  DashboardScreen,
  SearchScreen,
  BlogPostScreen,
  ProfileScreen,
  OrderScreen,
  SigninScreen,
  SignupScreen,
  ContactScreen,
  AboutScreen,
  AllProducts,
  DiscountProductScreen,
  NailartProductScreen,
  ToolsProductScreen,
  ComboProductScreen,
  VerifyEmailScreen,
  ChangePassScreen,
  ForgotPassScreen,
} from './screens';
import { SearchBox, ProtectedRoute, AdminRoute } from './components';
import { getError, API_URL } from './utils.js';
import axios from 'axios';
import ScrollToTop from './components/ScrollToTop.js';
import BackToTopButton from './components/BactToTopButton.js';
import Footer from './components/Footer.js';
axios.defaults.withCredentials = true;

function App() {
  const { state, dispatch: ctxDispatch } = useContext(Store);
  const { userInfo } = state;

  const signoutHandler = () => {
    ctxDispatch({ type: 'USER_SIGNOUT' });
    localStorage.removeItem('userInfo');
    localStorage.removeItem('shippingAddress');
    localStorage.removeItem('paymentMethod');
    window.location.href = '/signin';
  };

  const [categories, setCategories] = useState([]);

  useEffect(() => {
    const fetchCategories = async () => {
      try {
        const { data } = await axios.get(`${API_URL}/api/products/categories`);
        setCategories(data);
      } catch (err) {
        toast.error(getError(err));
      }
    };
    fetchCategories();
  }, []);

  const navLinkStyles = ({ isActive }) => {
    return {
      color: isActive ? '#1e2024' : '#ffffff',
      background: isActive ? '#17a2b8' : '',
    };
  };

  return (
    <BrowserRouter>
      <div>
        <ToastContainer position="bottom-center" limit={1} />

        <header>
          <ScrollToTop />

          <div className="container-fluid p-0">
            <nav className="navbar navbar-expand-lg bg-dark navbar-dark py-2 py-lg-0 px-lg-5">
              <a href="/" className="navbar-brand d-block">
                <h1 className="m-0 display-4 text-uppercase primary-color">
                  Techy
                  <span className="text-white font-weight-normal">Ship</span>
                </h1>
              </a>
              <button
                type="button"
                className="navbar-toggler"
                data-toggle="collapse"
                data-target="#navbarCollapse"
              >
                <span className="navbar-toggler-icon"></span>
              </button>
              <div
                className="collapse navbar-collapse justify-content-between px-0 px-lg-3"
                id="navbarCollapse"
              >
                <div className="navbar-nav mr-auto py-0">
                  <NavLink
                    to="/"
                    className="nav-item nav-link"
                    style={navLinkStyles}
                  >
                    Home
                  </NavLink>
                  <NavLink
                    to="/allnews"
                    className="nav-item nav-link"
                    style={navLinkStyles}
                  >
                    News
                  </NavLink>
                  <NavDropdown title="Category" id="basic-nav-dropdown">
                    {categories.map((category) => (
                      <Nav.Item key={category} className="dropdown-item">
                        <LinkContainer
                          to={{
                            pathname: '/search',
                            search: `category=${category}`,
                          }}
                        >
                          <NavDropdown.Item>{category}</NavDropdown.Item>
                        </LinkContainer>
                      </Nav.Item>
                    ))}
                  </NavDropdown>
                  <a href="contact.html" className="nav-item nav-link">
                    Contact
                  </a>
                  {userInfo ? (
                    <NavDropdown title={userInfo.name} id="basic-nav-dropdown">
                      <LinkContainer to="/profile">
                        <NavDropdown.Item eventkey="4" as={Link}>
                          User Profile
                        </NavDropdown.Item>
                      </LinkContainer>

                      <NavDropdown.Divider />
                      <a
                        eventkey="6"
                        as={Link}
                        className="dropdown-item"
                        href="#signout"
                        onClick={signoutHandler}
                      >
                        Sign Out
                      </a>
                    </NavDropdown>
                  ) : (
                    <Nav.Link
                      eventkey="7"
                      as={Link}
                      className="nav-link"
                      to="/signin"
                    >
                      Sign In
                    </Nav.Link>
                  )}

                  {userInfo && userInfo.isAdmin ? (
                    <NavDropdown title="Admin" id="admin-nav-dropdown">
                      <LinkContainer to="/admin/dashboard">
                        <NavDropdown.Item>Dashboard</NavDropdown.Item>
                      </LinkContainer>
                      <LinkContainer to="/admin/blogs">
                        <NavDropdown.Item>Blogs</NavDropdown.Item>
                      </LinkContainer>
                      <LinkContainer to="/admin/users">
                        <NavDropdown.Item>Users</NavDropdown.Item>
                      </LinkContainer>
                    </NavDropdown>
                  ) : (
                    ''
                  )}
                </div>
                <div
                  className="input-group ml-auto d-none d-lg-flex"
                  style={{ width: '100%', maxWidth: '300px' }}
                >
                  <div className="input-group-append">
                    <SearchBox />
                  </div>
                </div>
              </div>
            </nav>
          </div>
        </header>
        <main>
          <div className="">
            <Routes>
              {/* <ScrollRestoration /> */}
              <Route path="/blog/:slug" element={<BlogPostScreen />} />
              <Route path="/search" element={<SearchScreen />} />
              <Route path="/signin" element={<SigninScreen />} />
              <Route path="/signup" element={<SignupScreen />} />
              <Route
                path="/:id/password-reset/:token"
                element={<ChangePassScreen />}
              />
              <Route path="/forgot-password" element={<ForgotPassScreen />} />
              <Route
                path="/:id/verify/:token"
                element={<VerifyEmailScreen />}
              />
              <Route
                path="/profile"
                element={
                  <ProtectedRoute>
                    <ProfileScreen />
                  </ProtectedRoute>
                }
              />
              <Route
                path="/map"
                element={
                  <ProtectedRoute>
                    <MapScreen />
                  </ProtectedRoute>
                }
              />

              <Route
                path="/order/:id"
                element={
                  <ProtectedRoute>
                    <OrderScreen />
                  </ProtectedRoute>
                }
              />
              <Route path="/contact" element={<ContactScreen />} />
              <Route path="/about" element={<AboutScreen />} />

              {/* Admin Routes */}

              <Route
                path="/admin/dashboard"
                element={
                  <AdminRoute>
                    <DashboardScreen />
                  </AdminRoute>
                }
              />

              <Route
                path="/admin/blogs"
                element={
                  <AdminRoute>
                    <BlogListScreen />
                  </AdminRoute>
                }
              />

              <Route
                path="/admin/orders"
                element={
                  <AdminRoute>
                    <OrderListScreen />
                  </AdminRoute>
                }
              />

              <Route
                path="/admin/users"
                element={
                  <AdminRoute>
                    <UserListScreen />
                  </AdminRoute>
                }
              />

              <Route
                path="/admin/blog/:id"
                element={
                  <AdminRoute>
                    <BlogEditScreen />
                  </AdminRoute>
                }
              />

              <Route
                path="/admin/user/:id"
                element={
                  <AdminRoute>
                    <UserEditScreen />
                  </AdminRoute>
                }
              />

              {/* nailArts */}
              <Route path="/nailarts" element={<NailartProductScreen />} />
              <Route
                path="/nailart/changepage"
                element={<NailartProductScreen />}
              />

              <Route path="/allproducts/changepage" element={<AllProducts />} />
              <Route path="/allnews" element={<AllProducts />} />
              <Route path="/changepage" element={<HomeScreen />} />
              <Route path="/" element={<HomeScreen />} />
            </Routes>
          </div>
        </main>
        <footer>
          <Footer />
          <div
            className="container-fluid py-4 px-sm-3 px-md-5"
            style={{ background: '#111111' }}
          >
            <p className="m-0 text-center">
              &copy; <a href="/">Techyship</a>. All Rights Reserved. Design by{' '}
              <a href="https://www.kimmotechnology.com">kimmotechnology</a>
            </p>
          </div>
          <BackToTopButton />
        </footer>
      </div>
    </BrowserRouter>
  );
}

export default App;
